import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { Observable, Subject } from 'rxjs';
import { forkJoin } from 'rxjs/observable/forkJoin';
import 'rxjs/add/observable/throw';
import { catchError, map } from 'rxjs/operators';


interface CategoryEndpoint {
  endpoint: string;
  name: string;
  subTitle: string;
}

interface EmailMessage {
  email: string;
  message: string;
}
@Injectable()

export class DataService {
  private dataChangeSubject: Subject<boolean>;
  public dataChanged$: Observable<boolean>;
  private baseURL: string = 'http://localhost:3003';
  public categoryEndpoints: CategoryEndpoint[];

  constructor(private http: HttpClient) {
    this.dataChangeSubject = new Subject<boolean>();
    this.dataChanged$ = this.dataChangeSubject.asObservable();

    this.categoryEndpoints = [
      { endpoint: `${this.baseURL}/api/catch`, name: 'catch', subTitle: "My Catches" },
      { endpoint: `${this.baseURL}/api/location`, name: 'location', subTitle: "Favorite Fishing Spots" },
      { endpoint: `${this.baseURL}/api/bait`, name: 'bait', subTitle: "Bait History" },
      { endpoint: `${this.baseURL}/api/lure`, name: 'lure', subTitle: "My Lures" },
      { endpoint: `${this.baseURL}/api/email`, name: 'email', subTitle: "email" }
    ];
  }


  fetchCategoryData(): Observable<{ name: string, items: any[] }[]> {
    const requests = this.categoryEndpoints.map(category =>
      this.http.get<any[]>(`${this.baseURL}/${category.endpoint}`).pipe(
        map(items => ({ name: category.subTitle, items })),
        catchError(this.handleError)
      )
    );
    return forkJoin(requests);
  }


  getEndPoint(subTitle: string) {
    const category = this.categoryEndpoints
      .find(ce => ce.subTitle === subTitle);
    const url = category ? category.endpoint : `Wrong SubTitle (${subTitle}) Provided for Endpoint.`;
    return url;
  }

  getItems(subTitle: string): Observable<{ subTitle: string, items: any[] }> {
    const url = this.getEndPoint(subTitle);
    return this.http.get<any[]>(`${url}`)
      .pipe(
        map(items => ({ subTitle, items })),
        catchError(this.handleError)
      );
  }

  addItem(subTitle: string, item: any): Observable<{ subTitle: string, items: any[] }> {
    const url = this.getEndPoint(subTitle);
    return this.http.post<any>(url, item).pipe(
      catchError(this.handleError),
      map(response => {
        console.log('Item added successfully:', response);
        this.dataChangeSubject.next(true);
        return { subTitle: subTitle, items: response };
      }),
      catchError(error => {
        console.error('Failed to add item:', error);
        this.dataChangeSubject.next(false);
        return Observable.throw(error);
      })
    );
  }

  removeItem(endpoint: string, id: string): Observable<void> {
    const url = this.getEndPoint(endpoint);
    return this.http.delete<void>(`${url}/${id}`)
      .pipe(
        catchError((error) => {
          console.error('Failed to remove item:', error);
          return Observable.throw(new Error('Failed to remove item'));
        })
      );
  }

  editItem(subTitle: string, item: any): Observable<{ subTitle: string, items: any[] }> {
    const url = this.getEndPoint(subTitle);
    return this.http.put<any>(`${url}/${item._id}`, item).pipe(
      catchError(this.handleError),
      map(response => {
        console.log('Item edited successfully:', response);
        this.dataChangeSubject.next(true);
        return { subTitle, items: response };
      })
    );
  }

  sendEmail(email: EmailMessage): Observable<any> {
    const url = this.getEndPoint('email');
    return this.http.post(url, email)
      .pipe(
        catchError(this.handleError),
        map(response => {
          console.log('Email sent successfully:', response);
          return response;
        })
      );
  }


  private handleError(error: HttpErrorResponse) {
    console.error('DataService Error:', error.message);
    return Observable.throw(() => new Error(`Error: ${error.message}`));
  }
}





